<template>
  <div>
        <!-- start page title -->
        <div class="row">
            <div class="col-12">
                <div class="page-title-box d-flex align-items-center justify-content-between">
                    <h4 class="mb-0 font-size-18">DRE - Demonstrativo de Resultado por Exercício</h4>
                    <div class="page-title-right">
                        <ol class="breadcrumb m-0">
                            <li class="breadcrumb-item" @click="imprimir_dre()"> Imprimir </li>
                        </ol>
                        
                    </div>

                </div>
            </div>
        </div>
        <!-- end page title -->
        <div class="row" style="padding:10px">
            <div class="col-sm-12 col-md-3">                  
                <label >Nível de Detalhamento  
                    <select @input="debounceBusca" v-model="filtro.nivel" class="custom-select custom-select-sm form-control form-control-sm">
                        <option :value="-1">Todos</option>
                        <option :value="0">0</option>
                        <option :value="1">1</option>
                        <option :value="2">2</option>
                        <option :value="3">3</option>
                        <option :value="4">4</option>
                        <option :value="5">5</option>
                        <option :value="6">6</option>
                        <option :value="7">7</option>
                        <option :value="8">8</option>
                        <option :value="9">9</option>
                    </select>
                </label>
            </div>
            <div class="col-sm-12 col-md-3">
                <label style="width:90%">Contas Zeradas:
                    <select @input="debounceBusca" v-model="filtro.somente_com_lancamento" class="form-control form-control-sm">
                        <option :value="1">Não</option>
                        <option :value="0">Sim</option>                                                    
                    </select>
                </label>
            </div>
            <div class="col-sm-12 col-md-4">
                <label style="width:90%">Ano Exercício:
                    <select @input="debounceBusca" v-model="filtro.ano" class="form-control form-control-sm">
                        <option v-for="ano in anos" :key="ano" :value="ano">{{ano}}</option>                                                  
                    </select>
                </label>                  
            </div>
        
        </div>
        <div class="row">
            <div class="col-12">
                <div class="card">
                    <div class="card-body">
                        
                          <div class="table-responsive">
                            <table class="table mb-0" >
                                <thead>
                                    <tr >
                                        <th> Conta </th>
                                        <th> Jan </th>
                                        <th> Fev </th>
                                        <th> Mar </th>
                                        <th> Abr </th>
                                        <th> Mai </th>
                                        <th> Jun </th>
                                        <th> Jul </th>
                                        <th> Ago </th>
                                        <th> Set </th>
                                        <th> Out </th>
                                        <th> Nov </th>
                                        <th> Dez </th>
                                        <th> Saldo </th>
                                    </tr>
                                </thead>
                                <tbody>       
                                    <tr class="odd item-raiz">
                                        <th>
                                            Totais
                                        </th>
                                        <th class="text-right" v-for="mes in receita[0].lancamentos" :key="mes.mes">
                                            <span :class="{'text-red':total_saldo_mes(mes.mes) < 0, 'text-blue':total_saldo_mes(mes.mes) >= 0 }">
                                                {{total_saldo_mes(mes.mes) | currency($store.state.moeda) }}
                                            </span>
                                        </th>
                                        <th class="text-right" >
                                            <span :class="{'text-red':total_saldo < 0, 'text-blue':total_saldo >= 0 }">
                                                {{total_saldo | currency($store.state.moeda) }}
                                            </span>
                                        </th>
                                    </tr>         
                                    <tr  class="odd" v-for="dado in receita" :key="dado.codigo" :class="{'item-raiz': dado.nivel == 1 }">
                                        <td>
                                                {{dado.codigo}} | {{dado.nome}}
                                        </td>
                                        <td  class="text-right" v-for="(lancamento,index) in dado.lancamentos" :key="index">
                                            {{(lancamento.credito - lancamento.debito) | currency($store.state.moeda) }}
                                        </td>
                                        <td  class="text-right" >
                                           {{(dado.credito - dado.debito) | currency($store.state.moeda) }}
                                        </td>
                                    </tr>
                                    <tr  class="odd" v-for="dado in despesa" :key="dado.codigo" :class="{'item-raiz': dado.nivel == 1 }">
                                        <td>
                                                {{dado.codigo}} | {{dado.nome}}
                                        </td>
                                        <td class="text-right" v-for="(lancamento,index) in dado.lancamentos" :key="index">
                                            {{(lancamento.credito - lancamento.debito) | currency($store.state.moeda) }}
                                        </td>
                                        <td class="text-right">
                                           {{(dado.credito - dado.debito) | currency($store.state.moeda) }}
                                        </td>
                                    </tr>
                                    
                                     <tr class="odd item-raiz">
                                        <th>
                                            Totais
                                        </th>
                                        <th class="text-right" v-for="mes in receita[0].lancamentos" :key="mes.mes">
                                            <span :class="{'text-red':total_saldo_mes(mes.mes) < 0, 'text-blue':total_saldo_mes(mes.mes) >= 0 }">
                                                {{total_saldo_mes(mes.mes) | currency($store.state.moeda) }}
                                            </span>
                                        </th>
                                        <th class="text-right" >
                                            <span :class="{'text-red':total_saldo < 0, 'text-blue':total_saldo >= 0 }">
                                                {{total_saldo | currency($store.state.moeda) }}
                                            </span>
                                        </th>
                                    </tr>       
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div> 
        </div>
  </div>
</template>

<script>
import { CaixaService } from '../../../services'
const caixaService = new CaixaService();


export default {
    data(){
        return{
            receita: [],
            despesa: [],
            timer:null,
            anos: [new Date().getFullYear()],
            moeda_padrao: null,
            filtro:{
                ano: new Date().getFullYear(),
                nivel: 4,
                somente_com_lancamento: 0,
            },
            debounce: null,            
        }   
    },
    methods: {
        debounceBusca: function(){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarDre();
            }, 800);
        },
        
        buscarDre: function(){
            let f = {...this.filtro};
            caixaService.dre(f).then(
                response => {
                    if(!this.moeda_padrao)
                        this.moeda_padrao = response.data.moeda_padrao;
                    this.receita = Object.values(response.data.receita).map(x => {
                        x.nivel = x.codigo.split('.').length;
                        return x;
                    });
                    this.despesa = Object.values(response.data.despesa).map(x => {
                        x.nivel = x.codigo.split('.').length;
                        return x;
                    });
                }
            )
        },
        total_receitas_mes(mes){
            if(this.receita.length == 0)
                return 0
            return this.receita[0].lancamentos[mes].credito - this.receita[0].lancamentos[mes].debito;
        },
        total_despesas_mes(mes){
            if(this.despesa.length == 0)
                return 0
            return this.despesa[0].lancamentos[mes].credito - this.despesa[0].lancamentos[mes].debito;
        },
        total_saldo_mes(mes){
            return this.total_receitas_mes(mes) + this.total_despesas_mes(mes);
        },

        buscarAnos: function(){
            caixaService.buscarAnos().then(
                response => {
                   this.anos = response.data
                }
            )
        },

        imprimir_dre: function(){
            let url = caixaService.link_dre(this.filtro);
            window.open(url,'_blank');
        }
    },
    computed:{
        
        total_receitas(){            
            if(this.receita.length == 0)
                return 0
            return (this.receita[0].credito - this.receita[0].debito);
        },
        total_despesas(){
            if(this.despesa.length == 0)
                return 0
            return (this.despesa[0].credito - this.despesa[0].debito);
        },
        total_saldo(){
            return this.total_receitas + this.total_despesas;
        },

        dreImpressaoUrl(){
            let f = {...this.filtro};
            return caixaService.dreImpressaoUrl(f);
        }
    },
    mounted(){
        
        this.buscarAnos();
        setTimeout(() => {
            this.buscarDre();
        }, 200);
    },
};
</script>
